import axiosInstance from "../plugins/axios";
import config from "./dataServiceConfig";
class RandevuDataService {
    constructor() { 
       
    }
    /*
    ilceListesi,
    ilListesi,
    kisiListesi,
    saatListesi,
    tarihListesi,
    uygunIslemTipiListesi
    */
    async ilListesiGetir() {

        let res = await axiosInstance.get(config["ilListesi"]);
        //let { data } = res.data;
        return await res.data;
    };
    async ilceListesiGetir(ilId) {

        let res = await axiosInstance.get(config.ilceListesi,{params:{sehirId:ilId}});
        //let { data } = res.data;
        return await res.data;
    };
    async uygunIslemTipiListesiGetir(mudurlukId) {

        let res = await axiosInstance.get(config.uygunIslemTipiListesi,{params:{mudurlukId:mudurlukId}});
        //let { data } = res.data;
        return await res.data;
    };
    async kisiListesiGetir() {

        let res = await axiosInstance.get(config.kisiListesi);
        //let { data } = res.data;
        return await res.data;
    };
    async tarihListesiGetir(mudurlukId,typeId) {

        let res = await axiosInstance.get(config.tarihListesi,{params:{mudurlukId:mudurlukId,turId:typeId}});
       
        return await res.data;
    };
    async saatListesiGetir(mudurlukId,typeId,tarih) {

        let res = await axiosInstance.get(config.saatListesi,{params:{mudurlukId:mudurlukId,islemTuruId:typeId,tarih:tarih}});
       
        return await res.data;
    };
    
    async mudurlukSec(data) {

        let res = await axiosInstance.post(config.mudurlukSec ,data);
       
        return await res.data;
    };
    async kimlikDogrula(data) {

        let res = await axiosInstance.post(config.kimlikDogrula ,data);
       
        return await res.data;
    };

    async randevuBaslat() {

        let res = await axiosInstance.get(config.randevuBaslat);
       
        return await res.data;
    };
    async kisiEkle(data) {

        let res = await axiosInstance.post(config.kisiEkle ,data);
       
        return await res.data;
    };
    async kisiRandevuSil(hash, cokluRandevuGrupId) {

        let res = await axiosInstance.delete(config.kisiRandevuSil ,{ params:{hash, cokluRandevuGrupId}});
       
        return await res.data;
    };

    async seciliMudurlukGetir() {

        let res = await axiosInstance.get(config.seciliMudurlukGetir );
       
        return await res.data;
    };
    async smsDogrula(data) {

        let res = await axiosInstance.post(config.smsDogrula,data );
       
        return await res.data;
    };
    async smsKoduOlustur() {

        let res = await axiosInstance.get(config.smsKoduOlustur );
        return await res.data;
    };
    async belgeListesiGetir(ids) {

        let res = await axiosInstance.get(config.belgeListesiGetir,{params:{ids:[1,2,3]}} );
        return await res.data;
    };
    async captchaOlustur() {

        let res = await axiosInstance.get(config.captchaOlustur );
        return await res.data;
    };
    
    async bildirimKaydet(data) {

        let res = await axiosInstance.post(config.bildirimKaydet,data );
       
        return await res.data;
    };
    
    async randevuBul(id,hash) {

        let res = await axiosInstance.get(config.randevuBul,{params:{id,hash}} );
       
        return await res.data;
    };
    
    async aktifRandevuGetir() {

        let res = await axiosInstance.get(config.aktifRandevuGetir );
       
        return await res.data;
    };
    
    async mudurlukGetir(id, time,hash) {

        let res = await axiosInstance.get(config.mudurlukGetir,{params:{id, time,hash}} );
       
        return await res.data;
    };
    
}
let instance = new RandevuDataService();
//export {  RandevuDataService as default };
export default instance;