<template >
  <div>
    <v-container style="max-width: 900px">
      <v-row>
        <v-col cols="12" md="12" sm="12">
          <v-alert
            border="left"
            colored-border
            color="success"
            elevation="2"
            type="success"
            class="mb-0"
            @click="endPointGoster()"
          >
            <h3 style="color: grey">SIRA AL !!</h3>
            <h4>
              Sayın {{ person.Ad }} {{ person.Soyad }} almış olduğunuz
              randevular aşağıdadır. Randevu saatinize 30 dakika kala sıra
              alabilirsiniz.
            </h4>
          </v-alert>
        </v-col>
      </v-row>

      <v-row justify="center">
        <v-col cols="12" md="12" sm="12">
          <v-card>
            <v-list dense>
              <v-list-item-group>
                <v-list-item v-for="(item, i) in appList" :key="i">
                  <v-list-item-icon>
                    <v-icon color="info">mdi-account</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title>
                      {{ item.ad }}
                      {{ item.soyad }}</v-list-item-title
                    >

                    <v-list-item-subtitle>
                      {{ item.islemTuru.deger }} randevusu
                    </v-list-item-subtitle>
                    <v-list-item-subtitle>
                      Tarih: {{
                        new Date(item.randevuZamani)
                          .toLocaleString("tr-TR")
                          .substring(0, 16)
                      }}
                    </v-list-item-subtitle>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-card>
        </v-col>
        <v-col>
          <v-btn x-large color="success" @click="siraAl()"> SIRA AL </v-btn>
        </v-col>

        <v-col cols="12" md="12" sm="12" v-if="showSub">
          <v-card>
            <v-textarea
              ref="info"
              outlined
              name="input-7-4"
              label="Info"
              :value="sub"
            ></v-textarea>
            <v-card-actions>
              <v-btn elevation="2" @click="copy()">KOPYALA</v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
    <v-dialog v-model="dialog" persistent max-width="400">
      <v-card>
        <v-card-title class="text-h5">
          Bildirim almak ister misiniz?
        </v-card-title>
        <v-card-text
          >Eğer bildirim göstermemize izin verirseniz randevu gününüz geldiğinde
          ve işlem için çağrıldığınızda sizi haberdar edebiliriz.</v-card-text
        >
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="dialog = false">
            Hayır
          </v-btn>
          <v-btn color="green darken-1" text @click="bildirimKaydet()">
            Evet
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import NotificationService from "../plugins/notification.js";
import RandevuDataService from "../dataServices/randevuDataService.js";
import jwt_decode from "jwt-decode";
export default {
  data: () => ({
    dialog: false,
    timeClickList: [],
    sub: {},
    showSub: false,
    lastTime: 0,
    person: {},
    appList:[]
  }),
  methods: {
    copy() {
      var input = this.$refs.info.$el.querySelector("textarea");
      input.select();

      document.execCommand("copy");
    },
    endPointGoster() {
      var time = new Date().getTime();
      if (time - this.lastTime < 1000) {
        this.lastTime = time;
        this.timeClickList.push(this.lastTime);
        if (this.timeClickList.length > 5) {
          this.showSub = true;
          this.timeClickList = [];
        }
      } else {
        this.showSub = false;
        this.timeClickList = [];
      }
      this.lastTime = time;
    },
    async bildirimKaydet() {
      this.dialog = false;
      var not = new NotificationService();
      var publicKey =
        "BIN2Jc5Vmkmy-S3AUrcMlpKxJpLeVRAfu9WBqUbJ70SJOCWGCGXKY-Xzyh7HDr6KbRDGYHjqZ06OcS3BjD7uAm8";
      var sub = await not.subscribe(publicKey);
      var token = localStorage.getItem("token");
      var tokenInfo = jwt_decode(token);
      this.person = tokenInfo;

      sub.TckimlikNo = tokenInfo.TcKimlikNo;
      this.sub = JSON.stringify(sub);
      var result = await RandevuDataService.bildirimKaydet(sub);
    },
    siraAl() {
      navigator.serviceWorker.ready.then(function (registration) {
        //alert("bildirim kaydedildi");
        // setTimeout(() => {

        registration.showNotification("SIRA NUMARASI", {
          body: "Sıra numaranız 102. Çağrıldığınızda bildirim alacaksınız.",
          icon: "./img/icons/favicon-16x16.png",
          vibrate: [200, 100, 200, 100, 200, 100, 200],
          tag: "vibration-sample",
          timestamp: new Date().setMinutes(new Date().getMinutes() + 42),
        });

        // }, 120000);
      });
    },
  },
  beforeMount() {
    var token = localStorage.getItem("token");
    if (!token) {
      this.$router.push({
        name: "kimlikdogrula",
        params: { returnpage: "siraal/" + this.$route.params.code },
      });
    } else {
      if (Notification.permission !== "granted") {
        this.dialog = true;
      } else {
        this.bildirimKaydet();
      }
    }
  },
  async mounted() {
    var code = this.$route.params.code;
    var decoded = window.atob(code);
    var split = decoded.split("|");

    var result = await RandevuDataService.mudurlukGetir(
      split[1],
      split[2],
      split[3]
    );

    var app = await RandevuDataService.aktifRandevuGetir();
    this.appList=app.data;
  },
};
</script>