<template >
  <div>
    <!-- <v-app-bar app src="https://picsum.photos/1920/1080?random"> </v-app-bar> -->

    <v-container style="max-width: 900px">
      <v-row>
        <v-col cols="12" md="12" sm="12">
          <v-alert
            border="left"
            colored-border
            color="success"
            elevation="2"
            type="success"
            class="mb-0"
            @click="endPointGoster()"
          >
            <h3 style="color: grey">TEBRİKLER</h3>
            <h5 style="color: grey">Randevunuz oluşturulmuştur</h5>
          </v-alert>
        </v-col>
      </v-row>

      <v-row justify="center">
        <v-col cols="12" md="8" sm="8">
          <v-card>
            <v-card-title class="text-h5">
              Bugün almış olduğunuz randevular
            </v-card-title>
            <v-list dense>
              <v-list-item-group>
                <v-list-item v-for="(item, i) in types" :key="i">
                  <v-list-item-icon>
                    <v-icon color="info">mdi-account</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title>
                      {{ item.person.ad }}
                      {{ item.person.soyad }}</v-list-item-title
                    >

                    <v-list-item-subtitle>
                      {{ item.multiType.map((k) => k.typeName).toString() }}
                    </v-list-item-subtitle>
                    <v-list-item-subtitle>
                      {{
                        new Date(item.seciliTarihSaat)
                          .toLocaleString("tr-TR")
                          .substring(0, 16)
                      }}
                    </v-list-item-subtitle>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-card>
          <v-card class="mt-4" v-if="showData">
            <v-card-title class="text-h5"> Link </v-card-title>
            <v-card-text>
              <a :href="linkData.linkHref" target="_blank">{{
                linkData.linkHref
              }}</a>
              <br />
              {{ linkData }}
            </v-card-text>
          </v-card>
        </v-col>

        <v-col cols="12" md="4" sm="4">
          <v-card class="mx-auto mb-0">
            <v-card-title class="text-h5"> Gerekli evraklar </v-card-title>
            <v-list dense>
              <v-list-item-group>
                <v-list-item v-for="(item, i) in belgeListesi" :key="i">
                  <v-list-item-icon>
                    <v-icon color="info">mdi-clipboard-text</v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    {{ item.adi }}
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import RandevuDataService from "../dataServices/randevuDataService.js";
export default {
  data: () => ({
    belgeListesi: [],
    lastTime: 0,
    timeClickList: [],
    showData: false,
    linkData: {},
  }),
  methods: {
    endPointGoster() {
      var time = new Date().getTime();
      if (time - this.lastTime < 1000) {
        this.lastTime = time;
        this.timeClickList.push(this.lastTime);
        if (this.timeClickList.length > 5) {
          this.showData = true;
          this.timeClickList = [];
        }
      } else {
        this.showData = false;
        this.timeClickList = [];
      }
      this.lastTime = time;
    },
  },
  async mounted() {
    var liste = await RandevuDataService.belgeListesiGetir();
    console.log(liste);
    this.belgeListesi = liste.data.fileList;
    this.linkData = liste.data.linkData;
    var linkHref =
      window.location.origin +
      process.env.VUE_APP_BASEURL +
      "#/qr/" +
      btoa("p|" + this.linkData.id + "|" + this.linkData.hash);
    this.linkData.linkHref = linkHref;
    console.log("href",this.linkData);
  },

  computed: {
    types() {
      return this.$store.state.selectedTypes;
    },
  },
};
</script>