<template >
  <div>
    <v-container style="max-width: 900px">
      <v-row>
        <v-col cols="12" md="12" sm="12">
          <v-alert
            border="left"
            colored-border
            color="grey"
            elevation="2"
            type="info"
            class="mb-0"
          >
            <h3 style="color: grey">
              Kimliğinizi doğrulayarak işleme devam edebilirsiniz.
            </h3>
            <h5 style="color: grey">
              Kimlik doğrulaması için istenen kişisel bilgileri giriniz.
            </h5>
          </v-alert>
        </v-col>
      </v-row>
      <v-row justify="center">
        <v-col md="12">
          <v-card elevation="4">
            <v-card-title class="text-h5"> Kimlik doğrulayınız </v-card-title>
            <v-card-text>
              <v-form v-model="valid">
                <v-container>
                  <v-row>
                    <v-col cols="12" md="6" sm="6">
                      <v-text-field
                        v-model="kisi.ad"
                        label="Ad"
                        required
                        outlined
                        dense
                      ></v-text-field>
                    </v-col>

                    <v-col cols="12" md="6" sm="6">
                      <v-text-field
                        v-model="kisi.soyad"
                        label="Soyad"
                        required
                        outlined
                        dense
                      ></v-text-field>
                    </v-col>

                    <v-col cols="12" md="6" sm="6">
                      <v-text-field
                        v-model="kisi.tcKimlikNo"
                        label="Kimlik No"
                        :counter="11"
                        required
                        outlined
                        dense
                      ></v-text-field>
                    </v-col>

                    <v-col cols="12" md="6" sm="6">
                      <v-text-field
                        v-model="kisi.telefonNo"
                        :counter="10"
                        label="Telefon (5XXXXXXXXX)"
                        required
                        outlined
                        dense
                      ></v-text-field>
                    </v-col>

                    <v-col cols="12" md="6" sm="6">
                      <v-menu
                        ref="menu"
                        v-model="menu"
                        :close-on-content-click="true"
                        transition="scale-transition"
                        offset-y
                        min-width="auto"
                        @click="menu = false"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="kisi.dt"
                            label="Doğum Tarihi"
                            readonly
                            outlined
                            dense
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker
                          v-model="kisi.dogumTarihi"
                          no-title
                          scrollable
                          locale="tr-tr"
                          @change="
                            kisi.dt = new Date(kisi.dogumTarihi)
                              .toLocaleString('tr-TR')
                              .substring(0, 10)
                          "
                        >
                          <!-- <v-spacer></v-spacer>
                          <v-btn text color="primary" @click="menu = false">
                            İptal
                          </v-btn>
                          <v-btn
                            text
                            color="primary"
                            @click="$refs.menu.save(kisi.dogumTarihi)"
                          >
                            Tamam
                          </v-btn> -->
                        </v-date-picker>
                      </v-menu>
                    </v-col>
                    <v-col cols="12" md="6" sm="6">
                      <div
                        style="
                          display: flex;
                          align-items: flex-start;
                          justify-content: space-around;
                        "
                      >
                        <v-img
                          outlined
                          height="40"
                          width="110"
                          :src="
                            'data:image/jpeg;base64,' +
                            captchaImage.captchBase64Data
                          "
                        ></v-img>
                        <v-tooltip bottom>
                          <template v-slot:activator="{ on, attrs }">
                            <v-btn
                              class="ml-2 mr-2"
                              v-on="on"
                              v-bind="attrs"
                              color="info"
                              outlined
                              height="40"
                              width="40"
                              @click="createCaptca()"
                            >
                              <v-icon>mdi-cached</v-icon>
                            </v-btn>
                          </template>
                          <span>Yenile</span>
                        </v-tooltip>
                        <v-text-field
                          v-model="kisi.dogrulamaKodu"
                          label="Doğrulama Kodu"
                          :counter="6"
                          required
                          outlined
                          dense
                        ></v-text-field>
                        
                      </div>
                    </v-col>
                  </v-row>
                </v-container>
              </v-form>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12" md="12" sm="12" class="pt-1">
          <v-card elevation="4">
            <v-card-actions>
              <v-btn color="success" class="" @click="geri"> GERİ </v-btn>
              <v-spacer></v-spacer>

              <v-btn color="success" class="" @click="giris"> İLERİ </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import RandevuDataService from "../dataServices/randevuDataService.js";
export default {
  data: () => ({
    list: ["Randevu Al", "Eski Randevuları Görüntüle"],
    kisi: {},
    valid: false,
    date: null,
    firstname: "",
    lastname: "",
    menu: false,
    // nameRules: [
    //   (v) => !!v || "Name is required",
    //   (v) => v.length <= 10 || "Name must be less than 10 characters",
    // ],
    email: "",
    // emailRules: [
    //   (v) => !!v || "E-mail is required",
    //   (v) => /.+@.+/.test(v) || "E-mail must be valid",
    // ],
    captchaImage: {
      captchBase64Data: "",
    },
  }),
  methods: {
    async giris() {
      if (this.$store.state.seciliMudurluk) {
        this.kisi.mudurlukId = this.$store.state.seciliMudurluk.id;
      }
      this.kisi.guid = this.captchaImage.guid;
      var result = await RandevuDataService.kimlikDogrula(this.kisi);
      console.log(result);
      if (result.status) {
        sessionStorage.setItem("token", result.token);
        localStorage.setItem("token", result.token);
        this.$router.push({ path: "../" + this.$route.params.returnpage });
      } else {
        await this.createCaptca();
        alert(result.message);
      }
    },
    geri() {
      this.$router.go(-2);
    },
    async createCaptca() {
      // if (this.needCaptcha) {
      console.log("createCaptca");
      var captca = await RandevuDataService.captchaOlustur();

      this.captchaImage = await captca.data;

      //this.timeout = setTimeout(this.createCaptca, 20000);
      //}
    },
  },
  async mounted() {
    this.needCaptcha = true;

    await this.createCaptca();
    if (!this.interval) {
      this.interval = setInterval(async () => {
        await this.createCaptca();
      }, 20000);
    }
  },
  destroyed() {
    console.log("destroyed");
    clearInterval(this.interval);
  },
};
</script>