<template >
  <div>
    <!-- <v-app-bar app src="https://picsum.photos/1920/1080?random"> </v-app-bar> -->
    <v-container style="max-width: 900px">
      <v-row justify="center">
        <v-col>
          <v-alert
            border="left"
            colored-border
            color="info"
            elevation="2"
            type="info"
            class="mb-0"
          >
            <h3 style="color: grey">QR KODU OKUTUNUZ</h3>
            <h4>
              Uygulamanın kamera erişimine izin vererek QR kod ile sıra alma
              işlemi yapabilirsiniz.
            </h4>
          </v-alert>
        </v-col>
      </v-row>
      <v-row justify="center">
         <v-col cols="12" v-if="mudurluk" >
         <v-alert
            border="left"
            colored-border
            color="success"
            elevation="2"
            type="success"
            class="mb-0"
            prominent
          >
            <h3 style="color: grey">TEBRİKLER QR KOD OKUMA İŞLEMİ BAŞARILI</h3>
           <div>

              <span v-if="kisi">Sayın {{ kisi.Ad }} {{ kisi.Soyad }}, </span>
              {{ mudurluk.aciklama }} ilçe nüfus müdürlüğüne
              hoşgeldiniz
           </div>
          </v-alert>
        </v-col>
        <v-col cols="12" v-if="kisi && !mudurluk">
          <v-card class="mx-auto mb-0">
            <v-card-title v-if="kisi && !mudurluk" class="mb-0"
              >Sayın {{ kisi.Ad }} {{ kisi.Soyad }} lütfen QR kodu
              okutunuz.</v-card-title
            >
            <!-- <v-card-title v-if="mudurluk" class="mb-0">
              <span v-if="kisi">Sayın {{ kisi.Ad }} {{ kisi.Soyad }}, </span>
              {{ mudurluk.aciklama }} ilçe nüfus müdürlüğüne
              hoşgeldiniz</v-card-title
            > -->

            <v-card-text>
              <div>
                <p class="error">{{ error }}</p>

                <qrcode-stream
                  v-if="kisi && !mudurluk"
                  @decode="onDecode"
                  @init="onInit"
                />
              </div>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>
<script>
import { QrcodeStream, QrcodeDropZone, QrcodeCapture } from "vue-qrcode-reader";
import NotificationService from "../plugins/notification.js";
import RandevuDataService from "../dataServices/randevuDataService.js";
import jwt_decode from "jwt-decode";

export default {
  components: {
    QrcodeStream,
    QrcodeDropZone,
    QrcodeCapture,
  },
  data: () => ({
    kisi: null,
    mudurluk: null,
    result: "",
    error: "",
  }),
  methods: {
    onDecode(result) {
      const url = new URL(result);
      var code = url.hash.replace("#/qr/", "");

      var decoded = window.atob(code);

      this.urlData(decoded);
    },

    async onInit(promise) {
      try {
        await promise;
      } catch (error) {
        if (error.name === "NotAllowedError") {
          this.error = "ERROR: you need to grant camera access permisson";
        } else if (error.name === "NotFoundError") {
          this.error = "ERROR: Bu cihazda kamera bulunamadı.";
        } else if (error.name === "NotSupportedError") {
          this.error = "ERROR: secure context required (HTTPS, localhost)";
        } else if (error.name === "NotReadableError") {
          this.error = "ERROR: is the camera already in use?";
        } else if (error.name === "OverconstrainedError") {
          this.error = "ERROR: installed cameras are not suitable";
        } else if (error.name === "StreamApiNotSupportedError") {
          this.error = "ERROR: Stream API is not supported in this browser";
        }
      }
    },
    async urlData(decoded) {
      var split = decoded.split("|");
      console.log(split);
      var t = split[0];
      if (t == "p") {
        var result = await RandevuDataService.randevuBul(split[1], split[2]);
        var tokenInfo = jwt_decode(result.token);
        this.kisi = tokenInfo;
        if (result) {
          sessionStorage.setItem("token", result.token);

          localStorage.setItem("token", result.token);
        }
      } else if (t == "m") {
        var result = await RandevuDataService.mudurlukGetir(split[1]);

        this.$store.commit("setMudurluk", { data: result.data });

        this.mudurluk = result.data;
        setTimeout(() => {
          this.$router.push({
            name: "siraal",
            params: { code: window.btoa(decoded) },
          });
        }, 5000);
      }
    },
  },
  computed: {},
  async mounted() {
    sessionStorage.removeItem("token");
    var kisiler = [
      {
        id: 10,
        typ: "p",
        tc: 123,
        adi: "Hasan Kıvılcım",
        randevu: true,
        mudurlukId: 12,
      },
      {
        id: 10,
        typ: "p",
        tc: 456,
        adi: "Mehmet Aybaş",
        randevu: true,
        mudurlukId: 12,
      },
      {
        id: 10,
        typ: "p",
        tc: 789,
        adi: "Ayşe Türk",
        randevu: true,
        mudurlukId: 10,
      },
      { id: 10, typ: "p", tc: 321, adi: "Mahmut Kıvılcım", randevu: false },
      { id: 10, typ: "p", tc: 654, adi: "Emine Sur", randevu: false },
      { id: 10, typ: "p", tc: 987, adi: "Ayfer Afakan", randevu: false },
    ];
    this.$store.commit("setKisiListesi", { data: kisiler });
    var ilceler = [
      {
        typ: "m",
        id: 10,
        adi: "Keçiborlu",
        gecerlimi: true,
        t: new Date().toJSON(),
        h: "1234567890yuiopasqwertdfghjklzxc",
      },
      {
        typ: "m",
        id: 11,
        adi: "Muratpaşa",
        gecerlimi: true,
        t: new Date().toJSON(),
        h: "123456uiertyhjklzqwxcopasdfg7890",
      },
      {
        typ: "m",
        id: 12,
        adi: "Çankaya",
        gecerlimi: true,
        t: new Date().toJSON(),
        h: "123459tyuio0qwerjklzxcp678asdfgh",
      },
      {
        typ: "m",
        id: 13,
        adi: "Yenimahalle",
        gecerlimi: false,
        t: new Date("2021-05-20T03:24:00"),
        h: "123tyuiop45678xc9erasdf0qwghjklz",
      },
    ];
    this.$store.commit("setIlceListesi", { data: ilceler });
    var code = this.$route.params.code;
    try {
      var decoded = window.atob(code);
      var split = decoded.split("|");
      var data = { typ: split[0], id: split[1], hash: split[2] };

      //var data = JSON.parse(window.atob(code));

      this.urlData(decoded);
    } catch (error) {
      console.error("hata", error);
    }
  },
};
</script>